import React from 'react';
//import { isLocked, isAllowed } from '../services/Auth';

export default class TrackerBody extends React.Component {
    render() {
        return (<div>
            <div class="nav" style={{"user-select": "none"}}>
              <div class="fl-left">
                <span class="logo"><b>notevn</b>.com</span> <small>Tracker</small>
              </div>
              <div class="fl-right"> 
                <div class="options"> 
                  <a href="https://notevn.com" class="menu-item" title="" target="_blank" rel="noopener noreferrer"> 
                    <div class="tooltip">
                      <i class="ion-plus"></i> 
                      <span class="tooltiptext">Tạo note mới</span> 
                    </div>
                  </a>
                  <a href="javascript:void(0)" onClick={() => window.location.reload()} class="menu-item" title="" rel="noopener noreferrer"> 
                    <div class="tooltip">
                      <i class="ion-refresh"></i> 
                      <span class="tooltiptext">Làm mới</span> 
                    </div>
                  </a>
                  {/*!isLocked ? <a href="javascript:void(0)" onClick={() => extLock()} class="menu-item" title="" rel="noopener noreferrer"> 
                    <div class="tooltip">
                      <i class="ion-lock"></i> 
                      <span class="tooltiptext">Khóa</span> 
                    </div>
                  </a> : <a href="javascript:void(0)" onClick={() => extUnlock()} class="menu-item" title="" rel="noopener noreferrer"> 
                    <div class="tooltip">
                      <i class="ion-unlock"></i> 
                      <span class="tooltiptext">Mở khóa</span> 
                    </div>
                  </a>*/}
                </div> 
              </div>
              <div class="clear"></div>
            </div>
            <div class="major-wrap">
            <div class="editable ql-container ql-snow ql-disabled">
              <div class="ql-editor" data-gramm="false" contenteditable="false">
                {this.props.children}
              </div>
            </div>
          </div>
          </div>);
    }
};