/*global chrome*/
import React from 'react';
import './ionicons.css';
import './App.css';
import './Quill.css';
import './normalize.css';
import Storage from './services/Storage';
import Badge from './services/Badge';
import TrackerBody from './components/TrackerBody';

var today = new Date();
var storage = new Storage();
var _pinnedNote = "pinnedNoteList";
var _trackingNote = "noteTrackList";

function pinNote(noteUrl, currData, pinnedData) {
  for(var i = currData.length - 1; i >= 0; i--) {
    if(currData[i].url === noteUrl) {
      currData.splice(i, 1);
    }
  }
  pinnedData.unshift({
    url: noteUrl,
    time: today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds()+" "+today.getDate() + "/" + (today.getMonth()+1)+"/"+today.getFullYear()
  });
  storage.set(currData, _trackingNote);
  storage.set(pinnedData, _pinnedNote);
}

function unpinNote(noteUrl, currData, pinnedData) {
  for(var i = pinnedData.length - 1; i >= 0; i--) {
    if(pinnedData[i].url === noteUrl) {
      pinnedData.splice(i, 1);
    }
  }
  currData.unshift({
    url: noteUrl,
    time: today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds()+" "+today.getDate() + "/" + (today.getMonth()+1)+"/"+today.getFullYear()
  });
  storage.set(currData, _trackingNote);
  storage.set(pinnedData, _pinnedNote);
}

function removePinnedNote(noteUrl, pinnedData, numOfNotes) {
  let defaultValue = pinnedData.length;
  for(var i = pinnedData.length - 1; i >= 0; i--) {
    if(pinnedData[i].url === noteUrl) {
      pinnedData.splice(i, 1);
    }
  }
  if(defaultValue > pinnedData.length){
    storage.set(pinnedData, _pinnedNote);
    Badge.updateText(numOfNotes-1);
  }
}

function removeNote(noteUrl, currData, numOfNotes) {
  let defaultValue = currData.length;
  for(var i = currData.length - 1; i >= 0; i--) {
    if(currData[i].url === noteUrl) {
      currData.splice(i, 1);
    }
  }
  if(defaultValue > currData.length){
    storage.set(currData, _trackingNote);
    Badge.updateText(numOfNotes-1);
  }
}

function App() {
  var noteTrack = [];
  var pinnedNote = [];
  noteTrack = storage.get("noteTrackList");
  pinnedNote = storage.get("pinnedNoteList");

  if(!Array.isArray(noteTrack) || !Array.isArray(pinnedNote)) {
    storage.set([], _trackingNote);
    storage.set([], _pinnedNote);
    console.log(Array.isArray(noteTrack));
    return (
    <TrackerBody>
      <div>Có lỗi đã xảy ra, dữ liệu của bạn đã bị xóa vì bị nghi ngờ có người đã can thiệp vào quá trình bảo quản dữ liệu! Vui lòng click vào <strong>nút làm mới</strong> ở phía trên</div>
    </TrackerBody>
    );
  }
  else if(noteTrack === null && 
    pinnedNote === null || 
    noteTrack.length === 0 && 
    pinnedNote.length === 0)
  return (
    <TrackerBody>
      <div>Bạn chưa tạo note nào. Hãy click vào <strong>dấu cộng</strong> phía trên để tạo note mới nhé!</div>
    </TrackerBody>
  );
  else
  return (
    <TrackerBody>
          {pinnedNote.length !== 0 && pinnedNote.map(noteUrl => <ul> 
            <li>
              <a href={'https://notevn.com/' + noteUrl["url"]} target="_blank" rel="noopener noreferrer">
                {noteUrl["url"]}
              </a> ({noteUrl["time"]} - <a href="#" onClick={() => unpinNote(noteUrl["url"], noteTrack, pinnedNote)} rel="noopener noreferrer">Bỏ ghim</a> - <a href="#" onClick={() => removePinnedNote(noteUrl["url"], pinnedNote, (noteTrack.length + pinnedNote.length))} rel="noopener noreferrer">Gỡ</a>)
            </li>
          </ul>
          )}
          {noteTrack.length !== 0 && noteTrack.slice(0, 10).map((noteUrl, index) => 
            <ul>
              <li>
                <a href={'https://notevn.com/' + noteUrl["url"]} target="_blank" rel="noopener noreferrer">
                  {noteUrl["url"]}
                </a> ({noteUrl["time"]} - <a href="#" onClick={() => pinNote(noteUrl["url"], noteTrack, pinnedNote)} rel="noopener noreferrer">Ghim</a> - <a href="#" onClick={() => removeNote(noteUrl["url"], noteTrack, (noteTrack.length + pinnedNote.length))} rel="noopener noreferrer">Gỡ</a>)
              </li>
            </ul>
          )}
    </TrackerBody>
  );
}

export default App;
